.nc-toast-out{
    box-shadow: 0px 3px 12px 0px #4a515d40;
    position: fixed;
    min-width: 36px;
    background: #fff;
    min-height: 36px;
    line-height: 36px;
    padding: 0 15px;
    right: -100%;
    transition: all .3s;
    .nc-toast{
        &-inner{
            min-width: 100px;
            display: inline-block;
            text-align: center;
        }
        &-close{
            cursor: pointer;
            color: #0073e1;
            &:hover{
                color: #f44336;
            }
        }
        
    }
    &.in{
        right: 0;
    }
    &.success{
        color: #4caf50;
        border: 1px solid #4caf50;
        .nc-toast{
            &-close:hover{
                color: #4caf50;
            }
        }
    }
    &.error{
        color: #f44336;
        border: 1px solid #f44336;
        .nc-toast{
            &-close:hover{
                color: #f44336;
            }
        }
    }
    &.warning{
        color: #ff9800;
        border: 1px solid #ff9800;
        .nc-toast{
            &-close:hover{
                color: #ff9800;
            }
        }
    }

}

